<template>
    <el-container class="container">
        <el-aside>
            <el-menu router unique-opened background-color="#545c64" text-color="#fff" active-text-color="#ffd04b"
                     :default-active="activePath||this.$route.path">
                <template v-for="item of menuList">
                    <template v-if="item.children&&item.children.length">
                        <el-submenu :index="item.path" :key="item.path">
                            <template slot="title">
                                <i class="el-icon-location"></i>
                                <span>{{item.title}}</span>
                            </template>
                            <el-menu-item :index="subMenu.path" v-for="subMenu of item.children" :key="subMenu.path">
                                <i class="el-icon-menu"></i>
                                <span slot="title">{{subMenu.title}}</span>
                            </el-menu-item>
                        </el-submenu>
                    </template>
                    <template v-else>
                        <el-menu-item :index="item.path" :key="item.path">
                            <i class="el-icon-menu"></i>
                            <span slot="title">{{item.title}}</span>
                        </el-menu-item>
                    </template>
                </template>
            </el-menu>
        </el-aside>
        <el-main>
            <router-view></router-view>
        </el-main>
    </el-container>
</template>

<script>

  import menuList from './menus'

  export default {
    name: 'App',
    data() {
      return {
        menuList: menuList,
        activePath: ''
      }
    },
    watch: {
      "$route": function () {
        const {path} = this.$route.query
        if (path) {
          this.activePath = path
        } else {
          this.activePath = ''
        }
      }
    }
  }
</script>

<style>
    .container {
        height: 100%;
    }

    .el-menu {
        height: 100%;
    }
</style>
